import Dropdown from './Dropdown';
import '../styles/css/About.css';

/**
 * This is the component for the about page
 */

function AboutComponent() {
  return (
    <main className="about">
      <div className="about--pic"></div>
      <div className="dropdowns">
        <Dropdown
          title="Fiabilit"
          content={
            <p>
              Les annonces postes sur Kasa garantissent une fiabilit totale.
              Les photos sont conformes aux logements, et toutes les
              informations sont rgulirement vrifies par nos quipes.
            </p>
          }
        />
        <Dropdown
          title="Respect"
          content={
            <p>
              La bienveillance fait partie des valeurs fondatrices de Kasa. Tout
              comportement discriminatoire ou de perturbation du voisinage
              entranera une exclusion de notre plateforme.
            </p>
          }
        />
        <Dropdown
          title="Service"
          content={
            <p>
              Nos quipes se tiennent  votre disposition pour vous fournir une
              exprience parfaite. N'hsitez pas  nous contacter si vous avez
              la moindre question.
            </p>
          }
        />
        <Dropdown
          title="Scurit"
          content={
            <p>
              La scurit est la priorit de Kasa. Aussi bien pour nos htes que
              pour les voyageurs, chaque logement correspond aux critres de
              scurit tablis par nos services. En laissant une note aussi bien
               l'hte qu'au locataire, cela permet  nos quipes de vrifier
              que les standards sont bien respects. Nous organisons galement
              des ateliers sur la scurit domestique pour nos htes.
            </p>
          }
        />
      </div>
    </main>
  );
}

export default AboutComponent;
